// eslint-disable-next-line no-undef
import React, { Component } from "react"
import './Lexicon.css'

// eslint-disable-next-line no-undef
class LexiconFR extends Component{

    constructor(props){
        super(props);
        this.state = {}
    }

    render(){
        return(
            <>
                <div className="lexicon-title">
                    <b>FAQ</b>
                </div>
                <div className="lexicon-description">
                    &emsp;&emsp;Bienvenue  la section FAQ, dans cette section se trouvera plusieurs questions et rponses afin d'aider les utilisateurs  naviguer au travers de ce site Web efficacement. Il sera possible de trouver une liste de dfinitions de termes utilise dans les autres pages, une description de l'utilisation des pages 'Analyser', 'Comparer' et 'Rechercher', puis en conseillant plusieurs articles afin de vous mener  un meilleur processus d'investissement disponible  la page 'Apprendre'.
                    <br/>
                    <br/>&emsp;&emsp;<b>Lexique:</b>
                    <br/>
                    <br/>
                    &emsp;&emsp;Comme le langage utilis en finance peut tre trs complexe pour les nouveaux investisseurs, une liste de dfinitions de plusieurs termes sera fourni. Veuillez tenir compte que la liste de mots restera en anglais, car certains termes deviennent moins clair une fois traduit.
                </div>
                <div className="lexicon-list">
                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>52 Week Range</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le '52 Week Range' indique l'cart du prix de l'action lors des derniers 52 semaines. On y trouve le prix le bas suivi du prix le plus lev sur cette mme priode de temps.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Previous Close</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Previous Close' indique le prix que l'action avait lors de la fermeture des marchs boursiers la journe prcdente  16h EST. 
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Open</b>
                        </div>
                        <div className="lexicon-box-description">
                            Similaire  'Previous Close', 'Open' signifie le prix de l'action lors de l'ouverture des marchs boursiers  9h30 EST.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Ask</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le terme 'Ask' signifie le prix que les acheteurs demandent ainsi que le nombre d'acheteurs  ce moment prsent. La vraie valeur d'une action se situe entre le prix des acheteurs et demandeurs.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Bid</b>
                        </div>
                        <div className="lexicon-box-description">
                            Similaire  'Ask', 'Bid' signifie le prix que les vendeurs demandent ainsi que le nombre de vendeurs  ce moment prsent. La vraie valeur d'une action se situe entre le prix des acheteurs et demandeurs.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Day's Range</b>
                        </div>
                        <div className="lexicon-box-description">
                            Similaire au '52 Week Range', le 'Day's Range' montre l'cart du prix lors de la journe ou celle prcdente si les marchs boursiers sont ferms.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>1 year Target Estimate</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le '1 year Target Estimate' est un estim du prix futur d'une action dans un an. Cet estim est fait par un regroupement d'analystes, mais il ne faut pas se fi entirement  leur expertise.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Today's Volume</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le terme 'Today's Volume' montre le nombre d'achat et de vente d'une action particulire. Si une action est vendue 50 fois et achete 50 fois, le volume sera de 100.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Average Volume</b>
                        </div>
                        <div className="lexicon-box-description">
                            Comme le 'Volume' est le nombre d'achat et de vente d'une action particulire, le 'Average Volume' calcule la moyenne du 'Volume' des trois derniers mois.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Market Cap</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Market Cap' d'une companie est une manire de calculer la valeur de la compagnie. Le calcule est simple, on mutliplie le nombre d'actions de la compagnie et le prix de l'action.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Earnings Per Share (EPS)</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Earnings Per Share' d'une acion est le nombre de profit que la compagnie fait par rapport  la valeur d'une action. Un 'EPS' plus haut ne veut rien dire par lui-mme, il faut le comparer au prix de l'action, voir le 'Price-to-Earning Ratio'.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Forward Dividend</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Forward Dividend' signifie le montant d'argent qu'une compagnie prvoit vers par action lors de la prochaine anne. On y trouve souvent un 'Dividend Yield' qui montre les dividendes verses en pourcentage de la valeur de l'action.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Price-to-Earnings Ratio</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Price-to-Earnings Ratio' montre le ratio entre le prix d'une action et le nombre de profit que cette action rapporte. En gnral, plus une action  un 'Price-to-Earnings' bas, plus il sera profitable pour l'acheteur.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Beta</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Beta' est un indicateur de volatilit d'une action. Un 'Beta' infrieur  1 signifie que l'action est moins volatile que les marchs boursieurs et un 'Beta' suprieur  1 signifie que l'action est plus volatile.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Earnings Date</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Earnings Date' est la date que la compagnie va prsenter ses profits et revenus au public, cela influence les indicateurs 'EPS' et du mme fait le 'Price-to-Earnings Ratio'.
                        </div>
                    </div>

                    <div className="lexicon-box">
                        <div className="lexicon-box-title">
                            <b>Ex-Dividend Date</b>
                        </div>
                        <div className="lexicon-box-description">
                            Le 'Ex-Dividend Date' est la date limite qu'un investisseur doit possd une action avant qu'il soit vers les dividendes. Tenir en compte que les dividendes sont verses, en gnral, un mois aprs le 'Ex-Dividend Date'.
                        </div>
                    </div>

                    <div className="lexicon-description">
                        <br/>
                        &emsp;&emsp;<b>Apprendre:</b>
                        <br/>
                        <br/>
                        &emsp;&emsp;Apprendre page description
                        <br/>
                        <br/>
                        &emsp;&emsp;<b>Analyser:</b>
                        <br/>
                        <br/>
                        &emsp;&emsp;Analyser page description
                        <br/>
                        <br/>
                        &emsp;&emsp;<b>Comparer:</b>
                        <br/>
                        <br/>
                        &emsp;&emsp;Comparer page description
                        <br/>
                        <br/>
                        &emsp;&emsp;<b>Rechercher:</b>
                        <br/>
                        <br/>
                        &emsp;&emsp;Rechercher page description
                        <br/>
                        <br/>
                    </div>

                    <div className="empty-space"></div>
                </div>
            </>
        )
    }
}

export default LexiconFR;